<template>
  <nv-card>
    <div class="flex flex-wrap-reverse items-center flex-grow justify-between mb-base">
      <div class="flex flex-wrap-reverse items-center">
        <vs-button v-if="parent.inwardCollectionTableData.length > 0" class="p-3 mr-4" @click="exportXls" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
        <vs-button v-if="parent.inwardCollectionTableData.length > 0" class="p-3 mr-4" @click="exportCsv" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
        <vs-button class="p-3 mr-4" @click="parent.refreshInwardCollectionTable" type="filled" icon="fa-sync" icon-pack="far">{{ $t('REFRESH') }}</vs-button>
      </div>
    </div>

    <ag-grid-vue class="ag-theme-balham ag-pivot-layout"
                 :gridOptions="gridOptions"
                 :rowData="parent.inwardCollectionTableData"
                 :sideBar="sideBar"
                 :statusBar="statusBar">
    </ag-grid-vue>
  </nv-card>
</template>
<script>
import PivotLayoutMixin from '@/mixins/commons/PivotLayoutMixin'
import { AgGridVue } from 'ag-grid-vue'

export default {
  name: 'inward-dc-accepted-pivot-layout',
  mixins: [PivotLayoutMixin],
  components: {
    AgGridVue
  },
  beforeMount () {
    this.gridOptions.columnDefs = [{
      headerName: this.$t('COLLECTION_REFERENCE'),
      field: 'documentNumber'
    }, {
      headerName: this.$t('COLLECTION_TYPE'),
      field: 'lcTypeName',
      enableRowGroup: true
    }, {
      headerName: this.$t('DRAWER_NAME'),
      field: 'beneficiaryName',
      enableRowGroup: true
    }, {
      headerName: this.$t('DATE_ADVISED'),
      field: 'adviseDate',
      filter: 'agDateColumnFilter',
      enableRowGroup: true,
      valueFormatter: this.formatDate
    }, {
      headerName: this.$t('CURRENCY'),
      field: 'currencyAbbreviation',
      enableRowGroup: true
    }, {
      headerName: this.$t('AMOUNT'),
      field: 'paymentAmount',
      type: 'numericColumn',
      filter: 'agNumberColumnFilter',
      valueFormatter: this.formatNumber,
      enableValue: true
    }]
  }
}
</script>
