import TradeFinanceService from '@/services/trade/TradeFinanceService'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let DcAcceptedTableMixin = {
  mixins: [ListLayoutMixin, LoadingMixin],
  data: function () {
    return {
      inwardCollectionTableData: [],
      inwardCollectionTableContainer: 'loading-container',
      inwardCollectionTableBusy: false,
      inwardCollectionTableTotalRows: 0,
      inwardCollectionTableCurrentPage: 1,
      inwardCollectionTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('LC_NUMBER'),
        field: 'documentNumber',
        format: 'Number'
      }, {
        headerName: this.$t('LC_TYPE'),
        field: 'lcTypeName'
      }, {
        headerName: this.$t('LC_BENEFICIARY'),
        field: 'beneficiaryName'
      }, {
        headerName: this.$t('EXPIRY_DATE'),
        field: 'expiryDate',
        format: 'Date'
      }, {
        headerName: this.$t('CURRENCY'),
        field: 'currencyAbbreviation'
      }, {
        headerName: this.$t('AMOUNT'),
        field: 'paymentAmount',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    inwardCollectionTableNoDataText: function () {
      return this.inwardCollectionTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    inwardCollectionTableTotalPages: function () {
      return Math.ceil(this.inwardCollectionTableTotalRows / this.inwardCollectionTablePerPage)
    },
    inwardCollectionTablePagingInfo: function () {
      return this.pagingInfo(this.inwardCollectionTableCurrentPage, this.inwardCollectionTablePerPage, this.inwardCollectionTableTotalRows)
    }
  },
  methods: {
    loadInwardCollectionTableData (loadingContainerId) {
      let self = this
      this.inwardCollectionTableBusy = true
      this.inwardCollectionTableContainer = loadingContainerId || this.inwardCollectionTableContainer
      this.startLoading(this.inwardCollectionTableContainer)
      TradeFinanceService.acceptedDcs({
      }).then(function (response) {
        self.stopLoading(self.inwardCollectionTableContainer)
        self.inwardCollectionTableBusy = false
        self.inwardCollectionTablePerPage = response.data.totalRows
        self.inwardCollectionTableTotalRows = response.data.totalRows
        self.inwardCollectionTableData = response.data.documentList || []
      }).catch(function (error) {
        self.stopLoading(self.inwardCollectionTableContainer)
        self.inwardCollectionTableBusy = false
        self.$emit('show-alert', 'danger', error.response.data.errors)
      })
    },
    refreshInwardCollectionTable () {
      this.loadInwardCollectionTableData()
    }
  },
  watch: {
    inwardCollectionTableCurrentPage: function () {
      this.refreshInwardCollectionTable()
    }
  }
}

export default DcAcceptedTableMixin
