<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`DcAccepted${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import DcAcceptedListLayout from '@/views/trade/inward-dc/inward-dc-accepted/inward-dc-accepted-browse/InwardDcAcceptedListLayout'
import DcAcceptedCardLayout from '@/views/trade/inward-dc/inward-dc-accepted/inward-dc-accepted-browse/InwardDcAcceptedCardLayout'
import DcAcceptedPivotLayout from '@/views/trade/inward-dc/inward-dc-accepted/inward-dc-accepted-browse/InwardDcAcceptedPivotLayout'
import DcAcceptedTableMixin from '@/mixins/trade/inward-dc/InwardDcAcceptedTableMixin'

export default {
  name: 'inward-dc-accepted-browse',
  mixins: [DcAcceptedTableMixin],
  components: {
    DcAcceptedListLayout,
    DcAcceptedCardLayout,
    DcAcceptedPivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadInwardCollectionTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
